import { Field, reduxForm } from 'redux-form';
import s from './ProfileEditForm.module.css';
import { Input, InputEditor } from '../../../Common/FormsControl/FormsControls';
import {requiredField, maxLengthCreator} from '../../../../utils/validators/validators';
import React, {useEffect, useState } from 'react';

const ProfileEditForm = (props) => {
   let [fullName, changeFullName] = useState(props.fullName)

   // debugger;
   return (
       <form className={s.profileEditForm} onSubmit={props.handleSubmit}> 
         <div>
           <Field component={InputEditor} name={'fullName'} value={fullName} {...{fullName: fullName}}/>
         </div>
      
         <div>
           <button >Save</button>
         </div>
       </form>
   );
 };
 
 const ReduxProfileEditForm = reduxForm ({
   form: 'profileEdit',
 }) (ProfileEditForm)
 
 export default ReduxProfileEditForm;