import s from './ProfileEdit.module.css';
import ReduxProfileEditForm from './ProfileEditForm/ProfileEditForm';
import { connect } from 'react-redux';
import { getIsAuth } from '../../../Redux/Selectors/authSelector';
import React, {useEffect, useState } from 'react';

const ProfileEdit = (props) => {
   let [fullName, changeFullName] = useState(props.login)

   // let formData = props.formData.values.fullName ? props.formData.values.fullName : 'null'
   return (
      <div>
         <div className={s.header}>
            <h1>Profile edit</h1>
         </div>
         <div>
            <ReduxProfileEditForm fullName={props.login}/>
         </div>
         <div>
            {/* {props.formData.values.fullName} */}
         </div>

      </div>
   );
};

let mapStateToProps = (state) => ({
   isAuth: getIsAuth(state),
   formData: state.form.profileEdit,
   login: state.auth.profileMe.login,

})
export default connect(mapStateToProps, {})(ProfileEdit);